import { useState } from "react";
import { Button, Space } from "antd";
import { useTranslation } from "react-i18next";
import { PlusCircleOutlined, MinusCircleOutlined, EditOutlined } from "@ant-design/icons";

import classes from "./Buildings.module.scss";
import { AddModal, Head } from "./components";
import useBuildingsColumns from "./BuildingsColumns";
import { Select, Table } from "components/shared/AntComponents";
import { useNavigate } from "react-router-dom";

type Props = {};

const data = Array(10).fill({
	id: 1,
	a: "Олой сити",
	b: 1,
	c: 2,
	d: 3,
	e: 4
});

const Buildings = (props: Props) => {
	const { t } = useTranslation();
	const columns = useBuildingsColumns();
	const [toggle, setToggle] = useState(false);
	const navigate = useNavigate();

	return (
		<div className={classes.main}>
			<Head />

			<Table
				rowKey={"id"}
				loading={false}
				columns={columns}
				dataSource={data}
				onRow={(record) => ({
					onClick: () => navigate("create-apartment")
				})}
				headerLeftFilter={<Select size="large" width={200} options={[]} placeholder={t("Мажмуа номи")} />}
				headerRightFilter={
					<Space size={20}>
						<Button size="large" type="default" style={{ paddingInline: 10 }}>
							<MinusCircleOutlined />
						</Button>

						<Button size="large" type="default" style={{ paddingInline: 10 }}>
							<EditOutlined />
						</Button>

						<Button onClick={() => setToggle(true)} size="large" style={{ borderRadius: 50 }} type="primary" icon={<PlusCircleOutlined />}>
							{t("Янги бинони қўшиш")}
						</Button>
					</Space>
				}
			/>

			{toggle && <AddModal toggle={toggle} onCancel={() => setToggle(false)} />}
		</div>
	);
};

export default Buildings;
