import { Button, Col, Input, Modal, Row } from "antd";
import { useTranslation } from "react-i18next";

import classes from "./AddModal.module.scss";
import { Select } from "components/shared/AntComponents";
import { Tabs, UploadFile } from "components/shared";
import { ITabs } from "components/shared/Tabs/Tabs";
import { useState } from "react";

type Props = {
	toggle: boolean;
	onCancel: () => void;
};

const AddModal = ({ toggle, onCancel }: Props) => {
	const { t } = useTranslation();

	const tabs: ITabs[] = [
		{ id: 1, label: t("Турар жой") },
		{ id: 2, label: t("Нотурар жой") }
	];

	const [activeTab, setActiveTab] = useState<ITabs>(tabs[0]);

	return (
		<Modal width={730} open={!!toggle} onCancel={onCancel} footer={null}>
			<div className={classes.main}>
				<div className={classes.title}>{t("Янги бино қўшиш")}</div>

				<div className={classes.wrapper}>
					<Row gutter={[30, 30]}>
						<Col md={12}>
							<Select size="large" className="w-100" placeholder={t("Мажмуа номи")} />
						</Col>
						<Col md={12}>
							<Select size="large" className="w-100" placeholder={t("Қулайликлар")} />
						</Col>
						<Col md={12}>
							<Input size="large" className="w-100" placeholder={t("Бино рақами")} />
						</Col>
						<Col md={12}>
							<Select size="large" className="w-100" placeholder={t("Подьездлар сони")} />
						</Col>
					</Row>

					<div className="d-f jc-c mt-30">
						<Tabs tabs={tabs} active={activeTab} setActive={setActiveTab} />
					</div>

					<div className="mt-30">
						<UploadFile />
					</div>
				</div>

				<div className="d-f jc-c mt-30 g-10">
					<Button onClick={onCancel} type="text" style={{ borderRadius: 50 }} size="large">
						{t("Бекор қилиш")}
					</Button>

					<Button type="primary" style={{ borderRadius: 50 }} size="large">
						{t("Сақлаш")}
					</Button>
				</div>
			</div>
		</Modal>
	);
};

export default AddModal;
