import { Spin } from "antd";
import { ApexOptions } from "apexcharts";
import ReactApexChart from "react-apexcharts";
import { useTranslation } from "react-i18next";
import { useMemo, useRef, useState } from "react";

import classes from "./Card.module.scss";

const colors = ["#8CC152", "#5AC8F5", "#5E5CE6", "#FCC02A", "#E9573F"];

interface ILabels {
	label: string;
	count: number;
	rate: number;
}

const Chart = () => {
	const { t } = useTranslation();

	const [series, setSeries] = useState<number[]>([11, 22, 13, 43, 23]);

	const labelsRef = useRef<ILabels[]>([
		{
			rate: 5,
			label: t("1 хона"),
			count: 11
		},
		{
			rate: 4,
			label: t("2 хона"),
			count: 22
		},
		{
			rate: 3,
			label: t("3 хона"),
			count: 13
		},
		{
			rate: 2,
			label: t("4 хона"),
			count: 43
		},
		{
			rate: 1,
			label: t("5 хона"),
			count: 23
		}
	]);

	const options: ApexOptions = useMemo(() => {
		return {
			chart: { id: "donut_graph" },
			plotOptions: {
				pie: {
					startAngle: -90,
					endAngle: 270,
					donut: {
						size: "65%",
						background: "transparent",
						labels: {
							show: false
						}
					}
				}
			},
			dataLabels: {
				enabled: true,
				offsetX: 0,
				style: {
					fontFamily: "Helvetica, Arial, sans-serif",
					fontWeight: "normal",
					fontSize: "16px",
					colors: ["white"]
				}
			},
			tooltip: {
				shared: true,
				intersect: false
			},
			stroke: { show: false },
			colors,
			fill: {
				colors,
				opacity: 1,
				type: "solid",

				gradient: {
					shade: "dark",
					type: "horizontal",
					shadeIntensity: 0.5,
					gradientToColors: undefined,
					inverseColors: true,
					opacityFrom: 1,
					opacityTo: 1
				},
				pattern: {
					style: "verticalLines",
					width: 6,
					height: 6,
					strokeWidth: 20
				}
			},
			labels: labelsRef.current.map((item) => `${item.label} ${item.count}`),
			legend: {
				position: "left",
				horizontalAlign: "left",
				showForSingleSeries: false,
				showForNullSeries: true,
				showForZeroSeries: true
			},
			responsive: [
				{
					breakpoint: 480,
					options: { chart: { width: 200 }, legend: { position: "bottom" } }
				}
			]
		};
	}, [t]);

	return (
		<Spin spinning={false}>
			<div className={classes.chart}>
				<ReactApexChart options={options} series={series} type="donut" height={200} />
			</div>
		</Spin>
	);
};

export default Chart;
