import { useMemo } from "react";
import { ColumnsType } from "antd/es/table";
import { useTranslation } from "react-i18next";

const useBuildingsColumns = () => {
	const { t } = useTranslation();

	const columns: ColumnsType<any> = useMemo(
		() => [
			{
				width: 70,
				title: "№",
				dataIndex: "id",
				className: "text-center",
				render: (_: any, __: any, i: number) => <>{i + 1}</>
			},
			{
				width: 250,
				title: t("Мажмуа номи"),
				align: "center",
				dataIndex: "a"
			},
			{
				width: 250,
				title: t("Бинолар рақами"),
				align: "center",
				dataIndex: "b"
			},
			{
				width: 250,
				align: "center",
				title: t("Қулайликлар"),
				dataIndex: "c"
			},
			{
				width: 250,
				align: "center",
				title: t("Подьездлар"),
				dataIndex: "d"
			},
			{
				width: 250,
				align: "center",
				title: t("фото"),
				dataIndex: "e"
			}
		],
		[t]
	);

	return columns;
};

export default useBuildingsColumns;
